import React from 'react';
import {useParams} from 'react-router-dom';
import RenderMarkdownDocument from './RenderMarkdownDocument';

const DocumentSysAdminPage: React.FC = () => {
    const {'*': pathParam} = useParams<{'*': string}>();
    const documentPath = pathParam || '';

    return (
        <>
            <div className="dashboard-header-controls">
                <h1 className="dashboard-title">System Admin Documentation</h1>
            </div>
            <RenderMarkdownDocument
                documentGroup="sys"
                documentPath={documentPath}
                basePath="/sysdocs/"
            />
        </>
    );
};

export default DocumentSysAdminPage;
